import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../lebedev/lebedev_1.jpg'} alt={""} />
        <div>
          <div className={"person_name"}>Илья Лебедев</div>
        </div>
      <div className={"textBlock"}>
      <p>Сидеть дома мне совсем не&nbsp;сложно. Радует то, что появилось свободное время. К&nbsp;тому&nbsp;же, самоизоляция ничем не&nbsp;отличается от&nbsp;моих обычных выходных.</p>
<p>Я&nbsp;начал лучше разбираться в&nbsp;сортах кофе. Углубился в&nbsp;изучение оружия времён гражданской войны в&nbsp;США. Начал больше интересоваться вестернами, немного лучше разбираться в&nbsp;истории. Узнал несколько новых рок групп.</p>
<p>Читаю книги &laquo;Звездные войны. Дарт Мол. Тёмный властелин&raquo; и&nbsp;&laquo;Кровь, пот и&nbsp;пиксели&raquo;.</p>
<p>Посмотрел фильм &laquo;Хороший, плохой, злой&raquo;. Хотелось&nbsp;бы посмотреть еще несколько вестернов.</p>
<p>Играю в&nbsp;&laquo;Far cry 3&raquo; и&nbsp;&laquo;God of&nbsp;War&raquo;.</p>
<p>Что касается общения с&nbsp;друзьями. Друзья, они и&nbsp;во&nbsp;время самоизоляции&nbsp;&mdash; друзья! Естественно, мы&nbsp;общаемся, но&nbsp;теперь всё время по&nbsp;телефону. Других вариантов нет.</p>
<p>Дистанционное обучение, конечно, проигрывает обычному обучению по&nbsp;эффективности.</p>
<p>Плюсы есть только для школьников. Можно прогуливать, искать ответы в&nbsp;интернете <nobr>и т. п.</nobr> Из&nbsp;минусов: понижена эффективность, возможные проблемы с&nbsp;техникой <nobr>и т. п.</nobr></p>
<p>Когда закончится изоляция, скорее всего, пойду в&nbsp;парикмахерскую.</p>
<p>Какие места в&nbsp;мире я&nbsp;хотел&nbsp;бы посетить? США, в&nbsp;особенности, <nobr>Лос-Анджелес</nobr> или <nobr>Нью-Йорк</nobr>. Швейцарию, Мексику, Кубу, Чили, Японию и&nbsp;Китай.</p>
<p>Конечно&nbsp;же, после пандемии будет тяжело всем. Все люди и&nbsp;страны будут долго оправляться от&nbsp;такого мощного удара по&nbsp;экономике и&nbsp;производству.</p>
<p>Изменятся&nbsp;ли люди? Люди начинают меняться тогда, когда решают, что у&nbsp;них это получится. Но&nbsp;не&nbsp;думаю, что такое может получиться.</p>
<p>Если выбирать на&nbsp;время карантина <nobr>какую-то</nobr> суперспособность, скорее всего, я&nbsp;бы хотел не&nbsp;спать. Мне вообще не&nbsp;нравится инициатива того, что ты&nbsp;должен несколько часов ничего не&nbsp;делать.</p>
      </div>
    </article>
  </Layout>
)

export default IndexPage



